<template>
  <div class="user" v-loading="loading">
    <el-form>
      <el-form-item label="名称">
        <el-input readonly v-model="user.name" />
      </el-form-item>
      <el-form-item label="口令">
        <el-input type="password" v-model="user.password" />
      </el-form-item>
      <el-form-item label="首页">
        <textarea
          style="height: 40vh; width: 100%; word-wrap: break-word"
          type="textarea"
          v-model="user.index"
        ></textarea>
      </el-form-item>
      <el-form-item label="关于">
        <textarea
          style="height: 40vh; width: 100%; word-wrap: break-word"
          type="textarea"
          v-model="user.about"
        ></textarea>
      </el-form-item>
      <el-form-item label="备注">
        <el-input v-model="user.note" />
      </el-form-item>
    </el-form>
    <div class="btns">
      <wsh-button type="success" showComfirm text="修改" @comfirm="update" />
      <wsh-button type="info" showComfirm text="登出" @comfirm="logout" />
    </div>
  </div>
</template>

<script>
import { userGet, userUpdate } from '@/api';
import { PopTip } from '@/utils/tip';
import { store } from '@/store';
const tokenStore = store

export default {
  name: 'User',
  data() {
    return {
      loading: false,
      user: {
        id: '',
        name: '',
        password: '*********',
        about: '',
        note: '',
        index: '',
      },
    };
  },
  mounted() {
    this.loading = true;
    userGet('admin')
      .then(res => {
        const { status, data } = res.data;
        if (status) {
          Object.assign(this.user, data);
        }
      })
      .finally(() => {
        this.loading = false;
      });
  },
  methods: {
    logout(){
      tokenStore.removeToken()
      location.reload()
    },
    update() {
      userUpdate(this.user).then(res => {
        const { status } = res.data;
        if (status) {
          PopTip.success('修改成功！');
        } else {
          PopTip.error('修改异常！');
        }
      });
    },
  },
};
</script>

<style scoped lang="scss">
.btns{
  display: flex;
  justify-content: space-around;
  width: 100%;
}
</style>
